.deteyling {
    background: url(../img/car.png) no-repeat right top;
    max-width: 1366px;
    height: 670px;
    margin-top: -60px;
    box-shadow: 290px 0 150px 46px #000 inset;
}
.deteyling-title {
    flex-direction: column;
}
.deteyling-title h2 {
    font-weight: 800;
    line-height: normal;
    font-size: 55px;
    color: #F1F1F1;
    width: 70%;
    margin-top: 145px;
}
.deteyling-title p {
    font-weight: 500;
    font-size: 11px;
    color: #BBBABA;
    width: 22%;
    line-height: 2.2;
    margin-bottom: 30px;
    margin-top: 25px;
}
.info {
    display: flex;
    margin-top: 180px;
}
.info .info-title {
    display: flex;
    color: #F1F1F1;
    justify-content: space-evenly;
    margin-left: 30px;
}
.info p {
    margin: 0;
    width: 157px;
    height: 42px;
    overflow: hidden;
}
.icon a:nth-child(1) {
    padding-right: 25px;
}
.icon a img:hover {
    opacity: 0.5;
}


@media screen and (max-width: 576px) {
    .deteyling {
        background: url(../img/Large-8629McLaren-Senna----7b-mobile.png) no-repeat right top;
        margin-top: -170px;
        box-shadow: 37px 0 195px 23px #000 inset;
    }
    .deteyling-title h2 {
        font-size: 34px;
        width: 95%;
        margin-top: 225px;
        margin-left: 15px;
        text-align: left;
    }
    .deteyling-title p {
        width: 95%;
        margin-bottom: 20px;
        margin-left: 15px;
        text-align: left;
    }
    .deteyling-item p {
        margin-top: 180px;
        margin-bottom: 40px;
    }
    .deteyling-item {
        text-align: center;
    }
    .info {
        margin-top: 25px;
        display: block;
        text-align: center;
    }
    .info-title {
        margin-top: 25px;
        display: block !important;
        text-align: center;
        margin-left: 0 !important;
    }
    .icon {
        display: flex;
    }
}