.footer {
    color: #fff;
    margin-top: 30px;
}
.find {
    padding-left: 85px;
    margin-top: 100px;
}
.find h2 {
    font-weight: 800;
    font-size: 37px;
    color: #E8E8E8;
}
.find h3 {
    font-weight: bold;
    font-size: 24px;
    letter-spacing: 0.05em;
    color: #E8E8E8;
    margin: 10px 0;
}
.find p {
    font-weight: 500;
    font-size: 13px;
    color: #BBBABA;
}
.find a {
    font-weight: 500;
    font-size: 13px;
    color: #BBBABA;
}
.left {
    height: 470px;
}
.map {
    opacity: 0.2;
}


@media screen and (min-width: 992px) and (max-width: 1280px) {
    .find {
        padding-left: 150px;
    }
}