.slider-block {
    height: 634px;
    background: url(/img/roadster-social.png) no-repeat;
    margin-bottom: 100px;
}
.text-inform {
    display: flex;
    margin-top: 130px;
}
.text-inform h2 {
    font-weight: 800;
    line-height: normal;
    font-size: 37px;
    color: #E8E8E8;
}
.text-inform p {
    font-weight: 500;
    line-height: 2;
    font-size: 11px;
    color: #BBBABA;
}
.text-info-item .line-block {
    justify-content: flex-start;
    margin-top: 20px;
}
.text-inform .line {
    margin-right: 25px;
}
.carousel {
    width: 100%;
    margin-top: 35px;
}
.carousel img {
    width: 315px;
    height: 315px;
}

.images {
    display: flex;
    align-items: center;
    margin-top: 70px;
}
.images img {
    margin: 0 11px;
    height: 320px;
}
.images .car {
    height: 400px;
}
.images .right {
    margin-right: 0;
}
.images .left {
    margin-left: 0;
}



@media screen and (max-width: 576px) {
    .slider-block {
        margin-top: 0px;
    }
    .text-inform {
        display: block;
        margin-top: 30px;
    }
    .carousel img {
        width: 100%;
        height: 250px;
    }
    .images {
        display: flex;
        flex-direction: column;
    }
    .images img {
        margin: 10px 0px;
        height: 320px;
    }
    .images .car {
        height: 320px;
    }
}
@media screen and (min-width: 992px) and (max-width: 1280px) {
    .images img {
        height: 290px;
        width: 90%;
        margin: 0 10px;
    }
    .images .car {
        height: 320px;
    }
}