.services {
    padding-top: 150px;
}
.services-text {
    width: 100%;
}
.services-text h2 {
    font-weight: 800;
    line-height: normal;
    font-size: 55px;
    text-align: center;
    color: #E8E8E8;
}
.services-text p {
    font-weight: 500;
    line-height: 2;
    font-size: 11px;
    text-align: center;
    color: #BBBABA;
    width: 475px;
    margin: 0 auto;
    padding: 0 95px;
    height: 49px;
}
.services-card {
    display: flex;
    margin: 0 auto;
}
.services-card .card img {
    width: 286px;
    height: 300px;
}
.services-card .card:nth-child(2) {
    margin: 0 60px;
}
.line {
    width: 77.62px;
    height: 0px;
    border: 1px solid #F93524;
    margin-top: 18px;
    background: rgb(249, 53, 36);
}
.line-block {
    display: flex;
    justify-content: space-evenly;
    margin-top: -10px;
}
.card-body {
    text-align: center;
}
.card-title {
    font-weight: 800;
    line-height: normal;
    font-size: 30px;
    letter-spacing: 0.05em;
    color: #E8E8E8;
}
.card {
    background-color: #000;
}
.card-text {
    font-weight: 500;
    line-height: 2;
    font-size: 11px;
    color: #BBBABA;
}
.line-block .btn {
    font-weight: 500;
    font-size: 14px;
    color: #F93524;
}
.line-block a:hover {
    color: #fff;
}
.request {
    display: none;
}
.img1 {
    background: url(../img/car-cleaning-team.png) no-repeat;
    height: 300px;
    box-shadow: 0px 0 135px 50px #000 inset;
}
.img2 {
    background: url(../img/Best-Car-detailing-kits.png) no-repeat;
    height: 300px;
    box-shadow: 0px 0 135px 50px #000 inset;
}
.img3 {
    background: url(../img/1463.png) no-repeat;
    height: 300px;
    box-shadow: 0px 0 135px 50px #000 inset;
}


@media screen and (max-width: 576px) {
    .services {
        margin-top: 400px;
        text-align: center;
        background: url(/img/service-mobile.png) no-repeat;
        background-size: 100vw;
        padding-top: 6px;
    }
    .services-text p {
        width: 95%;
        padding: 0;
    }
    .services-card {
        flex-direction: column;
    }
    .services-card .card:nth-child(2) {
        margin: 0;
    }
    .request {
        display: block;
        margin-top: 50px;
    }
}