.nav-item-href .nav-link {
    color: #BBBABA !important;
    font-weight: 500;
    line-height: normal;
    font-size: 12px;
    text-align: center;
    letter-spacing: 1em;
    text-transform: uppercase;
}
.nav-item-tel .nav-link {
    font-weight: 800;
    line-height: normal;
    font-size: 13px;
    text-align: center;
    letter-spacing: 0.5em;
    color: #F93524 !important;
}
.header {
    margin-top: -10px;
}
.header .row {
    display: block !important;
}
.nav-item-href {
    margin: 0 40px;
}
.nav-item-href a:hover {
    color: #fff !important;
}
.nav-item-tel {
    margin: 0 -20px 0 70px;
}
.modal-button {
    width: 234px;
    height: 67px;
    background: #F93524;
    margin-top: -20px;
}
.header .nav-link p:hover {
    color: #fff;
}
.header .collapse {
    margin-top: 15px;
}



@media screen and (max-width: 576px) {
    .nav-item-tel {
        margin: 0;
    }
    .navbar-collapse {
        text-align: center;
    }
    .navbar-collapse .modal-button {
        margin-top: 0px;
    }
    .header .navbar-dark {

    }
    .navbar-dark .navbar-toggler {
        border: none;
    }
}
@media screen and (min-width: 992px) and (max-width: 1280px) {
    .nav-item-href {
        margin: 0px 5px;
    }
    .nav-item-tel {
        margin: 0 0px 0 10px;
    }
}