.questions {
    background: url(/img/questions.png) no-repeat;
}
.questions h2 {
    font-weight: 800;
    line-height: normal;
    font-size: 37px;
    text-align: center;
    color: #FFFFFF;
}
.questions .card-body {
    background: #252525;
    color: #FFFFFF;
    text-align: left;
    line-height: 2;
    padding: 0 15px 5px 35px;
}
.questions .card-header {
    background: #252525;
    color: #FFFFFF;
}
.questions .card-header h2 {
    text-align: left;
}
.questions .show p {
    font-weight: 500;
    font-size: 11px;
    color: #FFFEFE;
}
.questions .card {
    margin-bottom: 17px;
}
form {
    display: flex;
    flex-direction: column;
    margin-top: -3px;
}
.name {
    width: 100%;
    height: 67px;
    border: 1px solid #FFFFFF;
    box-sizing: border-box;
    margin: 5px 0;
    font-weight: 500;
    line-height: normal;
    font-size: 16px;
    color: #FFFFFF;
    background: transparent;
    padding-left: 30px;
}
textarea {
    width: 100%;
    height: 237px;
    border: 1px solid #FFFFFF;
    box-sizing: border-box;
    margin: 5px 0;
    font-weight: 500;
    line-height: normal;
    font-size: 16px;
    color: #FFFFFF;
    background: transparent;
    padding-left: 30px;
    padding-top: 20px;
}
.button {
    width: 100%;
    height: 67px;
    background: #F93524;
    color: #fff;
    font-weight: 500;
    line-height: normal;
    font-size: 16px;
    border: none;
    margin: 5px 0;
}
.question-text {
    width: 100%;
    height: 110px;
    margin-top: 80px;
}
.mb-0 {
    display: flex;
    justify-content: space-between;
}
.mb-0 img {
    width: 12.27px;
    height: 20.66px;
    margin-top: 9px;
}
.mb-0 button {
    
}
.rotate {
    transform: rotate(90deg);
}
.rotate1 {
    transform: rotate(0deg) !important;
}
.img-rotate1 {
    transform: rotate(90deg);
}



@media screen and (max-width: 576px) {
    .questions {
        margin-top: 540vw;
        background-size: 100vw;
    }
    .question-text {
        margin-top: 0px;
    }
    .map div {
        width: 100% !important;
        height: 570px !important;
    }
    .find {
        padding-left: 0px;
        margin-top: 0px;
        margin-left: 15px;
        margin-bottom: 30px;
    }
}